.button {
  display: inline-flex;
  padding: 12px;
  margin-left: auto;
  background: #0057ff;
  border-radius: 4px;
  color: white;
  font-weight: 600;
  box-shadow: 0px 4px 32px rgba(0, 87, 255, 0.5);
  cursor: pointer;
  align-items: center;
}

