.body {
  background-color: white;
  position: fixed;
  top: 100px;
  right: 0;
  border-radius: 8px;
  border: 1px solid #e4e5e7;
  box-shadow: 0px 2px 8px rgba(52, 51, 51, 0.08);
  z-index: 5;
  margin: 32px;
}

.header_body {
  display: flex;
  padding: 32px;
  border-bottom: 1px solid #e4e5e7;
  align-items: center;
}

.header {
  font-size: 18px;
  font-weight: 700;
  margin-right: 16px;
}

.img {
  margin-left: auto;
  cursor: pointer;
}

.button {
  display: inline;
  color: #94979e;
  padding: 8px;
  font-size: 16px;
  cursor: pointer;
}

.button_avtive {
  border-bottom: 2px solid #0057ff;
  color: #0057ff;
  font-weight: 700;
  cursor: default;
  transition: all 0.2s ease;
}

.table {
  margin: 32px;
}
.table_row {
  display: grid;
  border-top: none;
  align-items: center;
  margin-bottom: 2px;
}

.table_row_header {
  background: #e4e5e7;
  border-radius: 8px;
  top: 0;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
}

.table_cell {
  padding: 19px 16px;
  font-size: 14px;
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}

.disable {
  display: none;
}
